.formSummaryError {
    color: red;
    margin: 5px;
}

.container {
    min-width: 1280px;
    min-height: 720px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.newPassContainer {
    min-width: 413px;
    min-height: 600px;
    background: #F9F9FE;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin: 20px;
}

.title {
    width: 163px;
    height: 39px;
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 26px;
    line-height: 39px;
    /* identical to box height */
    color: #2D2E46;
}

.subTitle {
    width: 244px;
    height: 33px;
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 33px;
    /* identical to box height */
    text-align: right;
    color: #2D2E46;
}

.info {
    width: 347px;
    height: 68px;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #2D2E46;
    opacity: 0.5;
    margin-bottom: 20px;
}

.inputs {
    display: flex;
    flex-direction: column;
    height: 100px;
    justify-content: space-between;
    margin-bottom: 50px;
}